import React, { Component } from 'react';
import Navbar from './components/Navbar'
import loadingAnimation from './animations/json/loading.json'
import { Redirect } from 'react-router-dom'
import Animation from './animations/Animations'

class Profile extends Component {
    constructor(props) {
        super(props);
        this.state = {
          displayed_form: '',
          logged_in: localStorage.getItem('username') ? true : false,
          username: '',
          fetched: false
        };
    }
    componentDidMount() {
        let fullUrl = this.props.location.pathname
        if (this.state.logged_in) {
        fetch(`https://cookieapi-development.up.railway.app/api${fullUrl}?format=json`)
        .then(res => res.json())
        .then(json => {
          this.setState({ 
              username: json.username,
              emailhash: json.emailhash,
              balance: json.balance,
              accountNumber: json.accountid,
              rank: json.rank,
              joined: json.date_joined.substring(0,7),
              fetched: true
             });
        }).catch((err) => {
            this.setState({
                username: null
            })
        });
    }
}
    render() {
        if (this.state.logged_in) {
        let emailHash = this.state.emailhash
            if (this.state.username) {
                return(
                    <div>
                        <Navbar name="Profile" />
                            <div className="profile-parent">
                                <div className="profile-box">
                                    <div className="profile-gradient">
                                        <img preload="true" className="profile-picture" alt="" src={`https://gravatar.com/avatar/${emailHash}?d=mp&size=100`} />
                                    </div>
                                        <h1 className="profile-username">{this.state.username.toUpperCase()}</h1>
                                    <div className="profile-details">
                                        <p>Rank: <b>#{this.state.rank}</b></p>
                                        <p>Account Number: <b>{this.state.accountNumber}</b></p>
                                        <p>Cookies: <b>{this.state.balance}</b></p>
                                        <p>Date Joined: <b>{this.state.joined}</b></p>
                                    </div>
                                </div>
                            </div>
                    </div>
                )
            } else if (this.state.username === null) {
                return(
                    <div>
                        <Navbar name="Profile" />
                        <div className="profile-parent">
                            <h1 className="user-notfound">User not found!</h1>
                        </div>
                    </div>
                )
        } else {
            return(
                <div>
                    <Navbar name="Profile" />
                    {this.state.fetched ? '' : <Animation name={loadingAnimation} size={500}/>}
                </div>
            )
            }
        } else {
            return(
                <div>
                    <Redirect to="/login"/>
                </div>
            )
        }
    }
}

export default Profile;