import React, { useState } from 'react';
import axios from 'axios';
import './css/default.css'
import reportJson from './animations/json/report.json'
import Navbar from './components/Navbar'
import Animation from './animations/Animations'

function Report() {
    const [serverState, setServerState] = useState({
        submitting: false,
        status: null,
        data: {
            name: "",
            subject: "",
            email: "",
            message: ""
        }
    })
    const handleServerResponse = (ok, msg, form) => {
        setServerState({
            submitting: false,
            status: { ok, msg },
            data: {
                name: "",
                subject: "",
                email: "",
                message: ""
            }
        })
        if (ok) form.reset();
    }
    const handleOnSubmit = (e) => {
        e.preventDefault()
        let form = serverState.data
        console.log(serverState)
        axios({
            method: "POST",
            url: "https://api.github.com/repos/Cookie-Money/cookie-development/issues",
            data: {
                owner: `${form.name} - ${form.email}`,
                title: form.subject,
                body: `Opened by ${form.name} (${form.email})\n\n${form.message}`,
            },
            headers: { 'Content-Type': 'application/json' },
            auth: {
                username: 'CookieMoneyGame',
                password: 'f70c1fc034d51c2a2bfe5c9a5d8e318d259457f7'
            }
        })
            .then(r => {
                handleServerResponse(true, "Your report has been sent. Thank you for the support during this inital stage!", e.target);
            })
            .catch(r => {
                handleServerResponse(false, `An Error Occurred: ${r.message}`, e.target);
            })
    }
    const handleChange = (e) => {
        e.preventDefault()
        setServerState({
            data: Object.assign({}, serverState.data, { [e.target.name]: e.target.value })
        })
    }
    return (
        <div className="contact-main">
            <Navbar name="Report" />
            <div className="contact-flex">
                <Animation name={reportJson} size={500} />
                <form onSubmit={handleOnSubmit} className="form-parent">
                    <input id="contact-name" type="name" name="name" required placeholder="Name" value={serverState.data.name} onChange={handleChange} />
                    <input id="contact-subject" type="subject" name="subject" required placeholder="Issue Subject" value={serverState.data.subject} onChange={handleChange} />
                    <input id="contact-email" type="email" name="email" required placeholder="Email" value={serverState.data.email} onChange={handleChange} />
                    <textarea id="contact-message" type="message" name="message" required placeholder="Description" value={serverState.data.message} onChange={handleChange} />
                    <button id="contact-button" type="submit" disabled={serverState.submitting}>
                        Submit
                </button>
                </form>
            </div>
            {serverState.status && (
                <p id="contact-success-msg" className={!serverState.status.ok ? "errorMsg" : ""}>
                    {serverState.status.msg}
                </p>
            )}
        </div>
    )
}

export default Report