import React, { Component }from 'react';
import {Redirect} from 'react-router-dom'
import Navbar from './components/Navbar'
import registerJson from './animations/json/register.json'
import loadingJson from './animations/json/loading.json'
import Animation from './animations/Animations'

class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            posted: false,
            animation: registerJson
        }
    }
    state = {
        username: '',
        password: ''
    };
    handle_change = e => {
        const name = e.target.name;
        const value = e.target.value;
        this.setState(prevstate => {
            const newState = { ...prevstate };
            newState[name] = value;
            return newState;
        });
    };
    handle_login = (e, data) => {
        this.setState({
            animation: loadingJson
        })
        e.preventDefault();
        fetch('https://cookieapi-development.up.railway.app/api/token-auth/', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(data)
        })
          .then(res => res.json())
          .then(json => {
            if (json.non_field_errors?.[0]) {
                alert(json.non_field_errors[0])
                this.setState({
                    animation: registerJson
                })
            } else {
                localStorage.setItem('token', json.token);
                localStorage.setItem('username', json.user.username);
                this.setState({
                  logged_in: true,
                  username: json.user.username,
                  posted: true,
                });
            }
          });
      };
    render() {
        return (
            <div>
                <Navbar name="Login" />
                {this.state.posted ? <Redirect to={'/profile/' + this.state.username} /> : ''}
                <div className="signup-flex">
                <Animation name={this.state.animation} size={400} />
                    <form className="signup-parent" onSubmit={e => this.handle_login(e, this.state)}>
                        <input maxLength="12" placeholder="Username" className="signup-username" required type="text" minLength="2" name="username" value={this.state.username} onChange={this.handle_change}/>
                        <input maxLength="56" placeholder="Password" className="signup-password" required type="password" minLength="6" name="password" value={this.state.password} onChange={this.handle_change}/>
                        {/* <input className="signup-button" type="submit"/> */}
                        <button className="signup-button" type="submit">Login</button>
                    </form>
                </div>
                <p className="signup-para">Don't have an account? <a classname="signup-link" href="/signup">Sign up</a></p>
            </div>
          )
      }
}
export default Login