import React, { Component } from 'react';
import Navbar from './components/Navbar'
import loadingAnimation from './animations/json/loading.json'
import Animation from './animations/Animations'

let leaders
let balance
let profilePicture


class Rank extends Component {
    constructor() {
        super();
        this.state = { 
            data: [],
            isFetching: false
         };
    }
    
    componentDidMount() {
        fetch(`https://cookieapi-development.up.railway.app/api/leaderboard/?format=json`)
        .then(res => res.json())
        .then(json => this.setState({ 
            data: json,
            isFetching: true,
         }));
    }
    render() {

        let leaderboardData = this.state.data
        console.log(leaderboardData)
        leaders = leaderboardData.map((d) => <a className="profile-link" href={'/profile/' + d.username}>{d.username.toUpperCase()}</a>);
        balance = leaderboardData.map((d) => <p key={d.username}>{d.balance}</p>);
        profilePicture = leaderboardData.map((d) => <a className="profile-link" href={'/profile/' + d.username}><img className="rank-picture" key={d.emailhash} alt="" src={`https://gravatar.com/avatar/${d.emailhash}?d=mp`}></img></a>);

        let rankArray = [];

        for (let i=1; i<leaderboardData.length + 1; i++) {
            rankArray.push(<p><b>{i}.</b></p>)
        }

        return(
            <div>
                <Navbar name="Rank" />
                {this.state.isFetching ? '' : <Animation name={loadingAnimation} size={300} />}
                <div className="rank-flex">
                        <div className="rank-number">
                            {rankArray}
                        </div>
                        <div className="leaders-img">
                            {profilePicture}
                        </div>
                        <div className="leaders-name">
                            {leaders}
                        </div>
                        <div className="leaders-balance">
                            {balance}
                        </div>
                </div>
            </div>
        )
    }
}

export default Rank